<script>
import config from '../config';
import BigNumber from "bignumber.js";
export default {
  name: 'Home',
   data() {
     return {
        bnb_price:BigNumber(0),
        spig:{
          price:BigNumber(0),
          liq:BigNumber(0),
          cap:BigNumber(0),
          balance:BigNumber(0),
          burn:BigNumber(0)
        },
        lpig:{
          price:BigNumber(0),
          liq:BigNumber(0),
          cap:BigNumber(0),
          balance:BigNumber(0),
          reward:BigNumber(0)
        },
        listData:[
          {name:'HT',icon:require('../assets/coins/ht.svg'),supply_apy:'0.0000','borrow_apy':'0.0000',supply:'0.0000','my_supply':'0.0000',borrow:'0.0000',my_borrow:'0.0000'},
          {name:'BTC',icon:require('../assets/coins/btc.svg'),supply_apy:'0.0000','borrow_apy':'0.0000',supply:'0.0000','my_supply':'0.0000',borrow:'0.0000',my_borrow:'0.0000'},
          {name:'ETH',icon:require('../assets/coins/eth.svg'),supply_apy:'0.0000','borrow_apy':'0.0000',supply:'0.0000','my_supply':'0.0000',borrow:'0.0000',my_borrow:'0.0000'},
          {name:'USDT',icon:require('../assets/coins/usdt.svg'),supply_apy:'0.0000','borrow_apy':'0.0000',supply:'0.0000','my_supply':'0.0000',borrow:'0.0000',my_borrow:'0.0000'},
          {name:'USDC',icon:require('../assets/coins/usdc.svg'),supply_apy:'0.0000','borrow_apy':'0.0000',supply:'0.0000','my_supply':'0.0000',borrow:'0.0000',my_borrow:'0.0000'},
          {name:'DOT',icon:require('../assets/coins/dot.svg'),supply_apy:'0.0000','borrow_apy':'0.0000',supply:'0.0000','my_supply':'0.0000',borrow:'0.0000',my_borrow:'0.0000'},
          {name:'FIL',icon:require('../assets/coins/fil.svg'),supply_apy:'0.0000','borrow_apy':'0.0000',supply:'0.0000','my_supply':'0.0000',borrow:'0.0000',my_borrow:'0.0000'},
          {name:'LTC',icon:require('../assets/coins/ltc.svg'),supply_apy:'0.0000','borrow_apy':'0.0000',supply:'0.0000','my_supply':'0.0000',borrow:'0.0000',my_borrow:'0.0000'}
        ],
     }
   },
   computed:{
      address(){return this.$store.state.address},
      address1(){return this.$store.state.address1}
   },
   watch: {
    'address' () {
      this.refresh();
    }
   },
   mounted() {
    if(this.address){
      this.refresh();
    }
   },
   methods: {
      openClaim() {
        this.$message({
          message: 'Comin Soon',
          type: 'warning'
        });
      },
      openSwap(){
        window.open("https://swap.safepig.finance","_blank");
      },
      async refresh(){
        await this.getBNBPrice();
        this.getSPIG();
        this.getLPIG();
      },
      async getBNBPrice(){
        const linkContract = new this.$store.state.web3.eth.Contract(config.link_abi,config.link_bnb);
        let price = await linkContract.methods.latestAnswer().call();
        this.bnb_price = price;
      },
      async getSPIG(){
        const lpContract = new this.$store.state.web3.eth.Contract(config.lp_pair_abi,config.lp_spig);
        let reserves = await lpContract.methods.getReserves().call();
        if(reserves) {
          let reserve0 = new BigNumber(reserves[0]);
          let reserve1 = new BigNumber(reserves[1]);
          let pow18 = new BigNumber(10).exponentiatedBy(new BigNumber(18));
          let pow9 = new BigNumber(10).exponentiatedBy(new BigNumber(9));
          let pool_value_bnb = new BigNumber(0);
          let spig_value = new BigNumber(0);
          if(reserve0.isGreaterThan(0) && reserve1.isGreaterThan(0)){
            let token0 = await lpContract.methods.token0().call();
            let token1 = await lpContract.methods.token1().call();
            if(token0 && token0 == config.spig) {
              pool_value_bnb = reserve1.dividedBy(pow18);
              spig_value = reserve0.dividedBy(pow9);
            }
            if(token1 && token1 == config.spig) {
              pool_value_bnb = reserve0.dividedBy(pow18);
              spig_value = reserve1.dividedBy(pow9);
            }
          }
          let spig_usdt_value = pool_value_bnb.multipliedBy(this.bnb_price).dividedBy(1000000000);
          this.spig.price = spig_usdt_value.dividedBy(spig_value);
          this.spig.liq = spig_usdt_value.multipliedBy(2);
          this.spig.cap= this.spig.price.multipliedBy(new BigNumber(config.spig_supply));
          this.spig.burn=new BigNumber(0);
          this.spig.balance=new BigNumber(0);
          const tokenContract = new this.$store.state.web3.eth.Contract(config.token_abi,config.spig);
          let result = await tokenContract.methods.balanceOf(this.address).call();
          this.spig.balance=new BigNumber(result).dividedBy(pow9);
      
          let b = await tokenContract.methods._burnTotal().call();
          let r = await tokenContract.methods.balanceOf("0x0000000000000000000000000000000000000001").call();
          this.spig.burn = (BigNumber(r).plus(b)).dividedBy(1000000000);
        }
      },
      async getLPIG(){
        const lpContract = new this.$store.state.web3.eth.Contract(config.lp_pair_abi,config.lp_lpig);
        let reserves = await lpContract.methods.getReserves().call();
        if(reserves) {
          let reserve0 = new BigNumber(reserves[0]);
          let reserve1 = new BigNumber(reserves[1]);
          let pow18 = new BigNumber(10).exponentiatedBy(new BigNumber(18));
          let pow9 = new BigNumber(10).exponentiatedBy(new BigNumber(9));
          let pool_value_bnb = new BigNumber(0);
          let spig_value = new BigNumber(0);
          if(reserve0.isGreaterThan(0) && reserve1.isGreaterThan(0)){
            let token0 = await lpContract.methods.token0().call();
            let token1 = await lpContract.methods.token1().call();
            if(token0 && token0 == config.lpig) {
              pool_value_bnb = reserve1.dividedBy(pow18);
              spig_value = reserve0.dividedBy(pow9);
            }
            if(token1 && token1 == config.lpig) {
              pool_value_bnb = reserve0.dividedBy(pow18);
              spig_value = reserve1.dividedBy(pow9);
            }
          }
          let spig_usdt_value = pool_value_bnb.multipliedBy(this.bnb_price).dividedBy(100000000);
          this.lpig.price = spig_usdt_value.dividedBy(spig_value);
          this.lpig.liq = spig_usdt_value.multipliedBy(2);
          this.lpig.cap=this.lpig.price.multipliedBy(new BigNumber(config.lpig_supply));
          this.lpig.balance=new BigNumber(0);
          this.lpig.reward=new BigNumber(0);
          const tokenContract = new this.$store.state.web3.eth.Contract(config.token_abi,config.lpig);
          let result = await tokenContract.methods.balanceOf(this.address).call();
          this.lpig.balance=new BigNumber(result).dividedBy(pow9);
        }
      }
   }
}
</script>

