import Vue from 'vue';
import Vuex from 'vuex';
import Web3 from 'web3';
Vue.use(Vuex);
export default new Vuex.Store({
  state: {
    web3:null,
    address:'',
    address1:'',
  },
  mutations: {
    setWeb3(state,payload) {
      state.web3 = payload.web3;
    },
    setAddress(state,payload) {
      let addr = payload.address;
      if (addr) {
         let str_a = addr.substr(0, 4);
         let str_b = addr.substr(addr.length - 4);
         addr = str_a + "..." + str_b;
      }
      state.address = payload.address;
      state.address1 = addr;
    }
  },
  actions: {
    async connect({commit}) {
      let t = this;
      try{
         if(window.ethereum) {
          window.ethereum.on('accountsChanged', function (accounts) {
            if(t.state.address!=accounts){
              location.reload();
            }
          });
          window.ethereum.on('networkChanged', function (chainId) {
            if(chainId==="56"){
              location.reload();
            }else{
              t.$message.error('Network Error,Please Switch BSC Mainnet');
            }
          });
          let chainId = await new Web3(window.ethereum).eth.net.getId()+"";
          if(chainId!=="56"){
            await ethereum.request({
              method:'wallet_addEthereumChain',params:[{
                chainId:'0x38',
                chainName:'BSC Mainnet',
                nativeCurrency:{name:'BNB',symbol:'BNB',decimals:18},
                rpcUrls:['https://bsc-dataseed.binance.org'],
                blockExplorerUrls:['https://bscscan.com']
              }]
            }).catch(error=>{
                t.$message.error('Network Error,Please Switch BSC Mainnet');
            });
            return;
          }
          commit('setWeb3',{web3:new Web3(window.ethereum)});
          let accounts = await window.ethereum.request({method:'eth_requestAccounts'});
          commit('setAddress',{address:accounts[0]});
        }
      }catch(e){
        if(e && e.code && e.code==-32002){
          return;
        }
        t.$message.error('Network Error,Please Switch BSC Mainnet');
      }
    },
  },
  modules: {

  }

});
